import { NextResponse } from "next/server";
import { ethers } from "ethers";
import { CertificatiABI } from "@/abi/CertificatiABI";

// Carica variabili d'ambiente lato server
import 'dotenv/config';

export async function POST(req: Request) {
  try {
    // Leggi hash dalla request
    const { hash } = await req.json();
    if (!hash || typeof hash !== "string") {
      return NextResponse.json({ error: "Hash mancante o non valido" }, { status: 400 });
    }

    // Verifica variabili d'ambiente
    const RPC_URL = process.env.RPC_URL;
    const WALLET_PRIVATE_KEY = process.env.WALLET_PRIVATE_KEY;
    const CONTRACT_ADDRESS = process.env.CONTRACT_ADDRESS;

    if (!RPC_URL || !WALLET_PRIVATE_KEY || !CONTRACT_ADDRESS) {
      return NextResponse.json({ error: "Variabili d'ambiente mancanti" }, { status: 500 });
    }

    // Crea provider con chainId esplicito
    const provider = new ethers.providers.JsonRpcProvider(RPC_URL, 11155111);

    // Controlla la rete
    try {
      const network = await provider.getNetwork();
      console.log("✅ Network rilevato:", network);
    } catch (err) {
      console.error("❌ Impossibile rilevare la rete:", err);
      return NextResponse.json({ error: "Impossibile rilevare la rete" }, { status: 500 });
    }

    // Crea wallet e contratto
    const wallet = new ethers.Wallet(WALLET_PRIVATE_KEY, provider);
    console.log("✅ Wallet pronto:", wallet.address);

    const contract = new ethers.Contract(CONTRACT_ADDRESS, CertificatiABI, wallet);
    console.log("✅ Contratto connesso:", contract.address);

    // Forza hash a bytes32
    const formattedHash = ethers.utils.hexZeroPad(hash.startsWith("0x") ? hash : "0x" + hash, 32);

    // Invia transazione
    const tx = await contract.aggiungiCertificato(formattedHash);
    const receipt = await tx.wait();

    return NextResponse.json({ txHash: tx.hash, blockNumber: receipt.blockNumber });

  } catch (err: any) {
    console.error("❌ Errore API aggiungi-certificato:", err);
    return NextResponse.json({ error: err?.message ?? "Errore interno" }, { status: 500 });
  }
}
